:root {
  --main-color: #00acdc;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

h1 {
  justify-self: center;
}

.logo {
  display: block;
}

.header {
  box-sizing: border-box;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.nav {
  flex-direction: row;
  justify-content: space-between;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav li {
  margin-right: 20px;
}

.nav li:last-child {
  margin-right: 0;
}

.link {
  color: #000;
  text-transform: uppercase;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: block;
}

.link:hover, .is-active {
  color: var(--main-color);
}

.link.is-active:after {
  content: "";
  background: var(--main-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: relative;
  bottom: 4px;
  left: 0;
}
